import React from 'react'
import {Card,Button,Input, Table,Pagination, Icon, Form} from 'antd'
import './index.css'
const columns = (downDetail) => [
{
  title: '开始时间',
  dataIndex: 'start_time',
}, {
  title: '公司名',
  dataIndex: 'org_name',
}, {
  title: '业务类型',
  dataIndex: 'buzz_type',
}, {
  title: '下载次数',
  dataIndex: 'demo_num',
}, {
  title: '用户数',
  dataIndex: 'user_num',
}, {
  title: '收入',
  dataIndex: 'income',
}, {
  title: '结算金额(元)',
  dataIndex: 'revenue',
},{
  title: '操作',
  dataIndex: '',
  render: (text, record) => (<Button className="noBorBtn" type="dashed" onClick={() => downDetail(record)}>下载明细</Button>)
}];

const data = [{
  key: 1,
  start_time: '2016-02-15',
  org_name: '是大家实打实的',
  buzz_type: '阿瑟东',
  demo_num: '106',
  user_num: '26',
  income: '2626',
  revenue: '2626',
},{
  key: 2,
  start_time: '2016-02-15',
  org_name: '是大家实打实的',
  buzz_type: '阿瑟东',
  demo_num: '106',
  user_num: '26',
  income: '2626',
  revenue: '2626',
},{
  key:3,
  start_time: '2016-02-15',
  org_name: '是大家实打实的',
  buzz_type: '阿瑟东',
  demo_num: '106',
  user_num: '26',
  income: '2626',
  revenue: '2626',
},{
  key: 4,
  start_time: '2016-02-15',
  org_name: '是大家实打实的',
  buzz_type: '阿瑟东',
  demo_num: '106',
  user_num: '26',
  income: '2626',
  revenue: '2626',
},{
  key: 5,
  start_time: '2016-02-15',
  org_name: '是大家实打实的',
  buzz_type: '阿瑟东',
  demo_num: '106',
  user_num: '26',
  income: '2626',
  revenue: '2626',
}];
@Form.create()
export default class CPCompanyManage extends React.Component{
  constructor(props) {
    super(props);
    this.state = {
      tableLoading: false,
      selectedRowKeys: [],
      pageNo: 1,
      gameFormData:{}
    }
    // this.addData = this.addData.bind(this)
    // this.editData = this.editData.bind(this)
    // this.deletData = this.deletData.bind(this)
    this.onShowSizeChange = this.onShowSizeChange.bind(this)
    this.changePageNo = this.changePageNo.bind(this)
    this.downDetail = this.downDetail.bind(this)
  }
  componentDidMount() {
    this.props.form.validateFields();
  }
  changePageNo(page, pagesize) {// 用来监听pageno变化
    console.log(page)
    console.log(pagesize)
  }
  onShowSizeChange(current, pageSize) { // 用来监听pagesize变化
    console.log(current, pageSize);
  }
  downDetail=(v)=>{
    console.log(v)
  }
  handleSubmit = (e) => {
    e.preventDefault();
    console.log(e)
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }
  render(){
    const {
      getFieldDecorator
    } = this.props.form;
    return(
      <div className="content fadein">
        <div>
          <div className="inputRight">
            <Form layout="inline" onSubmit={this.handleSubmit}>
              <Form.Item>
                {getFieldDecorator('company', {})(
                  <Input placeholder="请输入合同名称" />
                )}
              </Form.Item>
              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                >
                  查询
                </Button>
              </Form.Item>
            </Form>
          </div>
            {/* <Button type="primary" style={{marginBottom:"20px"}} onClick={this.addData()} shape="round" icon="plus">添加</Button> */}
            <Table loading={this.state.tableLoading} pagination={false} className="nopadtable" columns={columns(this.downDetail)} dataSource={data} />,
            <div className="mb20" style={{ textAlign: "right" }}>
              <Pagination showSizeChanger onChange={this.changePageNo} onShowSizeChange={this.onShowSizeChange} defaultCurrent={this.state.pageNo} total={500} />
            </div>
          </div>
      </div>
    )
  }
}




